<template>
    <div class="content" >
        <dowonPre v-if="contentShow.dowonPre"></dowonPre>
        <dowonLatest v-if="contentShow.dowonLatest"></dowonLatest>
    </div>
</template>
<script>

import dowonLatest from "@/views/WEB/content/customize/dowonLatest";
import dowonPre from "@/views/WEB/content/customize/dowonPre";
export default {
    components:{
        dowonLatest,
        dowonPre
    },
    watch: {
        '$route'(to, from) {
            this.cid = this.$route.params.cid;
            this.mcode = this.$route.params.mcode;
            this.setData()
        },

    },
    data() {
        return {
            mcode:'',
            cid:'',
            contentShow:{
                dowonLatest:false,
                dowonPre:false,
                about:false,
                slack:false
            }
        }
    },
    created() {
        this.cid = this.$route.params.cid;
        this.mcode = this.$route.params.mcode;
        this.setData()

    },
    methods: {
        setData() {
            for(const key in this.contentShow) {
                this.contentShow[key] = false;
            }
            this.contentShow[this.cid] = true;
        }

    }

}

</script>
